  .promo {
    width: 768px;
    background: none;
    color: #111111;
    margin: auto;
    height: 206px;
    padding-bottom: 610px;
  }
}
@media (min-width: 1440px) {
  .promo {
    position: relative;
    top: -78px;
    left: 0;
    width: 100%;
    height: 694px;
    background: url("../img/green-cat-desktop.jpg");
    background-position: right top;
    background-repeat: no-repeat;
    padding-bottom: 4px;
    overflow: hidden;
  }
}
@media (min-width: 1440px) and (min-resolution: 2dppx) {
  .promo {
    background: url("../img/green-cat-desktop@2x.jpg");
    background-size: 694px 694px;
    background-position: right top;
    background-repeat: no-repeat;
  }
}
.promo::after {
  content: "";
  background: #64a73d;
  width: 100%;
  height: 302px;
  position: absolute;
  z-index: -2;
  top: 0;
  left: 0;
}
@media (min-width: 768px) {
  .promo::after {
    display: none;
  }
}
@media (min-width: 1440px) {
  .promo::after {
    display: block;
    width: 100vh;
    height: 694px;
    left: 50%;
    top: 0;
  }
}
